<template>
<div class="cart">
    <div class="cartTitle">
        <div class="left">
            <router-link to="/">
                <i class="icon-back"></i>
            </router-link>
        </div>
        <div class="center">购物车</div>
        <div class="right">清空</div>
    </div>
    <div class="darkLine"></div>
    <div class="cartContent">
        <ul class="cartBox">
            <li>
                <div class="cartLeft">

                </div>
                <div class="cartCenter">
                    <p class="cartName">木制文具</p>
                    <p class="cartColor">原色</p>
                    <p class="cartPrice">￥678</p>
                </div>
                <div class="cartRight">
                    <i class="icon-close"></i>
                </div>
            </li>
        </ul>
    </div>
    <div class="line border-1px"></div>
    <div class="cartFooter">
        <div class="left">
            合计￥
        </div>
        <div class="right">
            结算
        </div>
    </div>
</div>
</template>

<script type="text/ecmascript-6">
export default {
    props: {
        selectedShop: {
            type: Object
        }
    }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
@import "../../common/stylus/mixin";
.cart
    width:100%
    .cartTitle
        width:100%
        position:fixed
        top:0
        left:0
        height:50px
        z-index:200
        background:#fff
        display:flex
        .left
            flex:0 0 20px
            width:20px
            line-height:50px
            margin-left:15px
            & > a
                text-decoration: none
                color:#333
        .center
            flex:1
            line-height:50px
            text-align:center
            font-size:14px
        .right
            flex:0 0 30px
            width:30px
            line-height:50px
            font-size:12px
            margin-right:15px
    .darkLine
        width:100%
        height:5px
        background:#EEEEEE
        margin-top:50px
    .cartContent
        width:100%
        position:absolute
        top:55px
        left:0
        bottom:50px
        overflow:hidden
        .cartBox
            width:100%
            & > li
                height:116px
                display:flex
                border-bottom:1px solid #EEEEEE
                .cartLeft
                    flex:0 0 100px
                    width:100px
                .cartCenter
                    flex:1
                    .cartName
                        font-size:14px
                        font-weight:600
                        margin-top:20px
                    .cartColor
                        font-size:12px
                        font-weight:600
                        color:#999999
                        margin-top:18px
                    .cartPrice
                        font-size:14px
                        font-weight:600
                        margin-top:25px
                .cartRight
                    flex:0 0 20px
                    width:20px
                    position:relative
                    .icon-close
                        position:absolute
                        bottom:10px
                        right:15px
    .cartFooter
        width:100%
        height:50px
        position:fixed
        bottom:0
        left:0
        z-index:200
        background:#fff
        display:flex
        font-size:14px
        .left
            flex:1
            line-height:50px
            padding-left:40px
        .right
            flex:0 0 124px
            width:124px
            background:#62CED7
            height:50px
            line-height:50px
            text-align:center
</style>


